html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

.body {
  margin: auto;
  max-width: 750px;
  font-family: "Lato", sans-serif;
  background-image: url("../images/background.jpg");
  font-size: 20px;
  font-weight: 700;
}

.importantText {
  display: inline-block;
  font-style: italic;
  text-align: center;
  width: 100%;
}
